import { Component, OnInit } from '@angular/core';
import { convertToInt } from '@totvs/thf-ui/utils/util';
import { ThfDialogService } from '@totvs/thf-ui';
import { ActivatedRoute, Router } from '@angular/router';
import { AtendimentoService } from './atendimento.service';

@Component({
  selector: 'app-atendimento',
  templateUrl: './atendimento.component.html',
  styleUrls: ['./atendimento.component.css']
})
export class AtendimentoComponent implements OnInit {
  
  qtde_atender: string = '1,00';
  agrupar_lotes_wms: boolean = false;
  fechar_item_requisicao: boolean = false;
  data_atendimento: string | Date = new Date();
  show_loading: boolean = false;

  item_atender: any;
  
  rowActions = {
    beforeSave: this.onBeforeSave.bind(this),
    afterSave: this.onAfterSave.bind(this),
    beforeRemove: this.onBeforeRemove.bind(this),
    afterRemove: this.onAfterRemove.bind(this),
    beforeInsert: this.onBeforeInsert.bind(this)
  };

  columns = [
    { property: 'cod-depos', label: 'Dep', readonly: true },
    { property: 'cod-localiz', label: 'Localização', readonly: true },
    { property: 'lote', label: 'Lote', readonly: true},
    // { property: 'validade', label: 'Validade', readonly: true},
    { property: 'cod-refer', label: 'Referência', readonly: true},
    { property: 'qtidade-dsp', label: 'Saldo Disp.', readonly: true},
    { property: 'qtd-atend', label: 'Qtde. Atender', required: true},
    // { property: 'em_proc', label: 'Em Proc', readonly: true},
  ];

  data = [];

  constructor(
    public thfDialog: ThfDialogService,
    private router: Router,
    protected activated_route: ActivatedRoute,
    private service: AtendimentoService,
  ) { 

  }

  ngOnInit() {
    this.item_atender = this.getParams();
    this.qtde_atender = this.item_atender['qtd-entreg-epi'];
    let parts = this.item_atender["dat-entreg-epi"].split('-');
    
    this.data_atendimento = new Date(parts[0], parts[1] - 1, parts[2])
    this.loadData();

  }

  onBeforeSave(row: any, old: any) {
    console.log('teste aqui', row, old);
    return row.occupation !== 'Engineer';
  }

  onAfterSave(row) {
    console.log('onAfterSave(new): ', row);
  }

  onBeforeRemove(row) {
    // console.log('onBeforeRemove: ', row);

    return true;
  }

  onAfterRemove(row) {
    // console.log('onAfterRemove: ', row);
  }

  onBeforeInsert(row) {
    // console.log('onBeforeInsert: ', row);

    return true;
  }
  submit(){
    let qtd_atender_total = 0;
    for (const item of this.data) {
      if(parseFloat(item['qtd-atend'].replace(',', '.')) > item['qtidade-dsp']){
        this.thfDialog.alert({title: 'Atenção', message: 'A quantidade a atender não pode ser maior que o saldo disponível.'});
        return this;
      }
      if(parseFloat(item["qtd-atend"].replace(',', '.')) < 0){
        this.thfDialog.alert({title: 'Atenção', message: 'A quantidade a atender não pode ser negativa.'});
        return this;
      }

      qtd_atender_total += parseFloat(item['qtd-atend'].replace(',', '.'));
    }

    if(qtd_atender_total !=  parseFloat(this.qtde_atender.replace(',', '.'))){
      this.thfDialog.alert({title: 'Atenção', message: 'A quantidade total atendida deve ser igual a quantidade a atender.'});
      return this;
    }

    if(!this.data_atendimento){
      this.thfDialog.alert({title: 'Atenção', message: 'A Data de atendimento deve ser informada.'});
      return this;
    }

    this.setShowLoading(true);

    let saldo = [];

    for (const item of this.data) {
      saldo.push({
        "cdn-estab": item['cod-estabel'],        
        "cod-depos": item['cod-depos'],
        "cod-localiz": item['cod-localiz'],
        "cod-lote": item['lote'],
        "qtd-atend": parseFloat(item['qtd-atend'].replace(',', '.'))
      });
    }

    let tt_param = {
      'cdn-funcionario': parseInt(this.item_atender['cdn-funcionario']) ,  
      'ep-codigo': this.item_atender['cdn-empresa'] ,  
      'cod-estabel': this.item_atender['cod-estabel'] ,  
      'cod-epi': this.item_atender['cod-epi'] ,  
      'cdn-fornecedor': parseInt(this.item_atender['cdn-fornecedor']) ,  
      'dat-entreg-epi': this.data_atendimento, 
      'tp-assinatura': parseInt(this.item_atender['tipo_autenticacao']), 
      'assinatura': this.item_atender['autenticacao'], 
      "it-codigo": this.data[0]['it-codigo'],
      usuar_trans: "", 
      saldo: saldo
    }


    let data_envio = {
      metodo: "atendimentoEPI",
      'paramEPI': tt_param
    }

    
    this.service.apiCrudEpi(data_envio, (response)=>{

      if(response.items[0].hasOwnProperty('cod-erro')){
        let string_error = '';
        for (let error of response.items) {
          string_error = string_error + error['desc-erro'] + '. ';
        }
        this.thfDialog.alert({title: 'Atenção', message: string_error});
        this.setShowLoading(false);
        return this;
      }

      this.setShowLoading(false);
      this.thfDialog.alert({title: 'Sucesso', message: "Atendimento realizado com sucesso!"});
      this.router.navigate(['epi-entregues']);
      
    },
    (error)=>{
      console.log('errooooooo', error);
      //TODO: informar erro com dialog
      this.setShowLoading(false);
      return null;
    } )


  }

  
  public getParams(property?:string){
    let a:any=this.getActivatedRoute();
    let b:any=a.queryParams;   
    let v=b.getValue();
    
    if(!property){ return v;}
    if(!v.hasOwnProperty(property)){
       
        return null;
    }
    return v[property];
  }

  public getActivatedRoute(){ return this.activated_route;}

  getShowLoading(){ return this.show_loading; }
  setShowLoading(v){
    this.show_loading = v;
    return this;
  }

  loadData(){
    this.setShowLoading(true);
    let tt_param = {
      'cdn-funcionario': parseInt(this.item_atender['cdn-funcionario']) ,  
      'ep-codigo': this.item_atender['cdn-empresa'] ,  
      'cod-estabel': this.item_atender['cod-estabel'] ,  
      'cod-epi': this.item_atender['cod-epi'] ,  
      'cdn-fornecedor': parseInt(this.item_atender['cdn-fornecedor']) ,  
      'dat-entreg-epi': this.data_atendimento
    };

    let data_envio = {
      metodo: "saldoEPI",//TODO: Rever o método que tem que chamar
      'paramEPI': tt_param
    }

    this.service.apiCrudEpi(data_envio, (response)=>{
      //TODO: verificar como será o retorno para tratar caso de erro
      if(response.total == 0){
        this.thfDialog.alert({title: 'Atenção', message: 'Não existe saldo para atendimento!'});
        this.setShowLoading(false);
        return this;
      }

      if(response.items[0].hasOwnProperty('cod-erro')){
        let string_error = '';
        for (let error of response.items) {
          string_error = string_error + error['desc-erro'] + '. ';
        }
        this.thfDialog.alert({title: 'Atenção', message: string_error});
        this.setShowLoading(false);
        return this;
      }

      this.data = response.items;
      this.setShowLoading(false);      
    },
    (error)=>{
      console.log('errooooooo', error);
      //TODO: informar erro com dialog
    
      this.setShowLoading(false);
      return null;
    } )
    
  }

}
