import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ThfDialogService, ThfDynamicFormField } from '@totvs/thf-ui';

@Component({
  selector: 'app-configuracoes',
  templateUrl: './configuracoes.component.html',
  styleUrls: ['./configuracoes.component.css']
})
export class ConfiguracoesComponent implements OnInit {
  params: any = {
    empresa: '',
    estabelecimento: ''
  };
  fields: Array<ThfDynamicFormField> = [
    { property: 'empresa', label: 'Empresa padrão', gridColumns: 6, gridSmColumns: 12 },
    { property: 'estabelecimento', label: 'Estabelecimento padrão', gridColumns: 6, gridSmColumns: 12 }
  ];

  constructor(
    private router: Router,
    public thfDialog: ThfDialogService,
  ) { }

  ngOnInit() {
    this.params.empresa = localStorage.getItem('ent_epi_empresa') || '';
    this.params.estabelecimento = localStorage.getItem('ent_epi_estabelecimento') || '';
  }

  salvar(){
    localStorage.setItem('ent_epi_empresa', this.params.empresa);
    localStorage.setItem('ent_epi_estabelecimento', this.params.estabelecimento);
    this.thfDialog.alert({title: 'Sucesso', message: 'Configurações atualizadas com sucesso!'});
    this.router.navigate(['biometric-crud']);
  }

}
